.upload-form-container {
    max-width: 400px;
    margin: 50px auto;
    padding: 20px;
    border: 1px solid #ccc;
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    text-align: center;
}

.file-input-container {
    margin-bottom: 20px;
}

.upload-btn {
    background-color: #4CAF50;
    color: white;
    padding: 10px 20px;
    border: none;
    border-radius: 4px;
    cursor: pointer;
}

.upload-btn:disabled {
    background-color: #aaa;
}

.response-container {
    margin-top: 20px;
}

.error {
    color: red;
}

.response {
    color: green;
}
